export const utilService = {
    makeId,
    makeLorem,
    getRandomIntInclusive,
    getDate
}

function makeId(length = 6) {
    var txt = '';
    var possible = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';

    for (var i = 0; i < length; i++) {
        txt += possible.charAt(Math.floor(Math.random() * possible.length));
    }

    return txt;
}

function makeLorem(size = 100) {
    var words = ['The sky', 'above', 'the port', 'was', 'the color of television', 'tuned', 'to', 'a dead channel', '.', 'All', 'this happened', 'more or less', '.', 'I', 'had', 'the story', 'bit by bit', 'from various people', 'and', 'as generally', 'happens', 'in such cases', 'each time', 'it', 'was', 'a different story', '.', 'It', 'was', 'a pleasure', 'to', 'burn'];
    var txt = '';
    while (size > 0) {
        size--;
        txt += words[Math.floor(Math.random() * words.length)] + ' ';
    }
    return txt;
}

function getRandomIntInclusive(min, max) {
    min = Math.ceil(min);
    max = Math.floor(max);
    return Math.floor(Math.random() * (max - min + 1)) + min; //The maximum is inclusive and the minimum is inclusive 
}

function getDate(miliSec) {
    const monthNames = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
    const year = new Date(miliSec).getFullYear()
    const month = monthNames[new Date(miliSec).getMonth()]
    const day = new Date(miliSec).getDate()
    let hours = new Date(miliSec).getHours()
    hours = hours < 10 ? `0${hours}` : hours;
    let minutes = new Date(miliSec).getMinutes()
    minutes = minutes < 10 ? `0${minutes}` : minutes;
    let  time = 0
    if (hours >= 0 && hours < 12) time = `${hours}:${minutes} am`
    else time = `${hours}:${minutes} pm`
    let date = `${month} ${day}, ${year}, ${time}`
    return date
}

