import Axios from 'axios'
const axios = Axios.create({
    withCredentials: true
});
const BASE_URL = (process.env.NODE_ENV == 'production')
 ? '/api/toy'
 : 'http://localhost:3030/api/toy';


export const toyService = {
  query,
  getById,
  remove,
  add,
  update,
 
}


function query(filterBy) {
  return axios.get(BASE_URL,{params:filterBy})
.then(res => res.data)
}
function getById(toyId) {
  return axios.get(`${BASE_URL}/${toyId}`).then(res => res.data)
}

function remove(toyId) {
  return axios.delete(`${BASE_URL}/${toyId}`)
}
function add(toy) {
  console.log('add',toy);
  return axios.post(BASE_URL,toy).then(res => res.data)
}

function update(toy,toyId) {
  return axios.put(`${BASE_URL}/${toyId}`, toy).then(res => 
  {
    console.log('toyservice',toy,toyId);
   return  res.data
  }
 )
}

